<template>
  <div id="app" @click="click">
    <transition :name="transitionName">
      <router-view/>
    </transition>
  </div>
</template>

<script>
export default {
  data() {
    return {
      transitionName: "",
      step: 0
    };
  },
  watch: {
    //使用watch 监听$router的变化
    $route(to, from) {
      if (parseInt(to.query._t) < parseInt(from.query._t)) {
        this.transitionName = "slide-right";
      } else {
        this.transitionName = "slide-left";
      }
    }
  },
  created() {
    if (!location.host.includes("github")) {
      this.showV();
    }
  },
  methods: {
    click(e) {
      if (this.$route.path == "/") {
        // debugger
        var lastStep = this.step;
        if (e.pageY < window.innerHeight / 2) {
          if (this.step <= 1) {
            this.step++;
          }
        } else {
          if (this.step > 1 && this.step <= 3) {
            this.step++;
          }
        }
        if (this.step == lastStep) {
          if (e.pageX < window.innerWidth / 2) {
            if (this.step > 3 && this.step <= 5) {
              this.step++;
            }
          } else {
            if (this.step > 5 && this.step <= 7) {
              this.step++;
            }
          }
        }
        if (this.step == lastStep) {
          this.step = 0;
        } else if (this.step == 8) {
          this.$router.replace({ path: "/entry/index" });
        }
        console.log("click1", e.pageX, e.pageY, lastStep, this.step);
      }
    },
    showV() {
      window.vConsole = new window.VConsole();
    }
  }
};
</script>


<style lang="less">
body {
  background-color: #f2f2f2;
}
#app {
  margin: 0;
}
.slide-right-enter-active,
.slide-right-leave-active,
.slide-left-enter-active,
.slide-left-leave-active {
  will-change: transform;
  transition: all 0.3s;
  position: absolute;
  width: 100%;
  left: 0;
}

.slide-right-enter {
  transform: translateX(-100%);
}

.slide-right-leave-active {
  transform: translateX(100%);
}

.slide-left-enter {
  transform: translateX(100%);
}

.slide-left-leave-active {
  transform: translateX(-100%);
}
</style>
